export default {
  name: 'ArticleExternalLink',
  props: {
    href: {
      type: String,
      required: true
    },
    target: {
      type: String,
      default: '_blank'
    },
    color: {
      type: String,
      default: '#3a8fb7'
    }
  },
  computed: {
    linkStyle() {
      return {
        color: this.color,
        textDecoration: 'underline'
      }
    }
  }
}
